<template>
  <div id='AuthorizeManage'>
    <div class="white">
      <div class="bdbtm">
        <el-row>
          <span class="tit left"></span>
          <span class="right mt15 mr15 btngroupsx">
            <el-button size="mini"></el-button>
          </span>
        </el-row>
      </div>
      <el-row>
        <div class="flex4">
          <div class="demo-input-suffix">
            
            <el-select v-model="valuea" placeholder="">
              <el-option
                v-for="item in optionsa"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="demo-input-suffix">
              
             <el-select v-model="value" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="demo-input-suffix">
              
             <el-date-picker
                v-model="value1"
                type="datetime"
                placeholder="">
              </el-date-picker>
          </div>
          <div class="demo-input-suffix">
            <el-button size='mini' icon="el-icon-refresh-left"></el-button>
            <el-button size='mini' type="primary" icon="el-icon-search"></el-button>
          </div>
        </div>
      </el-row>

      <el-row>
        <div class="flex4">
          <div class="demo-input-suffix">
            
            <el-input
              placeholder="ID/"
              v-model="input1">
            </el-input>
          </div>
        </div>
      </el-row>
    </div>

    <div class="white mt15">
      <el-row>
          <span class="tit left"></span>
          <span class="right mt15 mr15 btngroupsx">
            <el-button type='primary' size="mini" @click="createOrder"></el-button>
            <el-button size="mini" icon="el-icon-receiving"></el-button>
          </span>
      </el-row>
      <div class="pd15">
        <el-table
          border
          :data="tableData"
          style="width: 100%">
          <el-table-column
          type="selection"
          width="55">
          </el-table-column>
          <el-table-column
            prop="date"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="address"
            label="">
          </el-table-column>
          <el-table-column
            prop="addressa"
            label="DCI">
          </el-table-column>
          <el-table-column
            prop="addressb"
            label="">
          </el-table-column>
          <el-table-column
            prop="addressc"
            label="/">
          </el-table-column>
          <el-table-column
            prop="addressd"
            label="">
          </el-table-column>
          <el-table-column
            prop="addresse"
            label="">
            <template slot-scope="scope">
              <i v-if="scope.row.addresse == true"></i>
              <i v-else></i>
            </template>
          </el-table-column>
          <el-table-column
            width="250"
            prop="addressf"
            label="">
            <template slot-scope="scope">
               <el-button
                type="text"
                @click="checkOrder(scope.row)"></el-button>
               <el-button
                type="text"
                @click="cancelAuth(scope.row)"></el-button>

            </template>
          </el-table-column>
        </el-table>
      </div>

      <div class="paginations">
        <el-pagination
          background
          @size-change="handleSizeChangeg"
          @current-change="handleCurrentChangeg"
          :current-page="currentPage3"
          :page-sizes="[10, 20, 30, 100]"
          :page-size="100"
          layout="prev, pager, next, sizes"
          :total="400">
        </el-pagination>
      </div>

    </div>
  </div>
</template>
<script>
export default {
  name: 'AuthorizeManage',
  data () {
    return {
      input1: '',
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      optionsa: [{
        value: '1',
        label: 'fs'
      }, {
        value: '2',
        label: 'fds'
      }, {
        value: '3',
        label: 'f'
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      value: '',
      valuea: '',
      value1: '',
      tableData: [{
        date: '2016-05-02',
        name: '',
        address: ' 1518 ',
        addressa: ' 1518 ',
        addressb: ' 1518 ',
        addressc: ' 1518 ',
        addressd: '',
        addresse: false,
        addressf: 'affa'
      },
      {
        date: '2016-05-02',
        name: '',
        address: ' 1518 ',
        addressa: ' 1518 ',
        addressb: ' 1518 ',
        addressc: ' 1518 ',
        addressd: '',
        addresse: true,
        addressf: 'affa'
      }],
      currentPage3: 2
    }
  },
  methods: {
    handleSizeChangeg (val) {
      console.log(`a ${val} `)
    },
    handleCurrentChangeg (val) {
      console.log(`a: ${val}`)
    },
    checkOrder (x) {
      console.log(x)
      console.log('')
      this.$router.push('AuthorizeDetail')
    },
    cancelAuth (f) {
      console.log(f)
      console.log('')
    },
    createOrder () {
      console.log('')
    }
  }
}
</script>
<style scoped>
  .white{
    padding-bottom: 15px;
  }
  .flex4  .demo-input-suffix{
    flex: 4;
    text-align: left;
    margin-left: 20px;
  }
  .demo-input-suffix .el-input,
  .demo-input-suffix .el-select{
    width: 200px;
  }
  .white .el-row{
    margin-bottom: 15px;
  }
  .demo-input-suffix button{
    float: right;
    margin-right: 15px;
  }
  .cell{
    display: flex;
  }
  .cell button{
    flex: 3;
  }
  i{
    font-style: normal;
  }
</style>



